.header {
  position: absolute;
  z-index: 100;
  top: 40px;
  left: 0;
  right: 0;
  @include lg-block {
    top: 20px;
  }
  @include md-block {
    top: 0;
    padding: 15px 0;
  }
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn {
    filter: drop-shadow(0px 6px 11px rgba(0, 0, 0, 0.44)) drop-shadow(0px 20px 64px rgba(72, 123, 255, 0.33));
    padding: 12px 20px;
    color: rgba(255, 255, 255, 0.4);
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
    // margin-left: 5%;
    // @include lg-block {
    //   margin-left: 3%;
    // }
    @include md-block {
      margin-left: auto;
    }
  }
  &__navbar {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    &[style*="display: block"]{
      display:flex!important;
    }
    @include md-block {
      height: 100vh;
      padding: 10px 0 30px;
      display: flex;
      display: none;
      flex-flow: column;
      justify-content: space-between;
      position: absolute;
      z-index: 100;
      background: black;
      top: 0;
      left: 0;
      right: 0;
      text-align: center;
      border-bottom: 2px solid #32CDEF;
      box-shadow: 0 2px 40px 4px #32CDEF;
    }
  }
}

.burger {
  background: none;
  border: none;
  outline: none;
  display: none;
  padding: 8px 4px;
  position: relative;
  z-index: 1000;
  @include md-block {
    display: block;
  }
  svg {
    opacity: 0.4;
    transition: 0.35s;
  }
  &_active svg {
    opacity: 1;
  }
}

.nav {
  margin: auto;
  @include md-block {
    // padding: 20px 0 30px;
  }
  a {
    padding: 5px;
    color: rgba(#fff, .6);
    @include md-block {
      display: block;
      padding: 15px;
      font-size: 20px;
    }
    &:hover {
      color: #fff;
    }
    & + a {
      margin-left: 50px;
      @include lg-block {
        margin-left: 20px;
      }
      @include md-block {
        margin-left: 0;
      }
    }
  }
}